.container-moviesimdb {
    display: flex;
    /* padding-top: 50px; */
    width: 100%;
    margin-left: 50px;

    
}

.container-moviesimdb img {
    width: 50%;
    height: auto;
    border-radius: 50px;
}

.container-moviesimdb h2 {
    font-size: 1.3rem;
    margin-left: 60px;   
}

.container-moviesimdb p {
    margin-left: 60px;
    font-size: 1.3rem;
}

.container-info {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 50%;
}

@media (max-width: 699px) {

    .container-moviesimdb {
        display: flex;
        flex-wrap: wrap;
        padding-top: 30px;
        width: 100%; 
        margin: 10px;  
    }
    
    .container-moviesimdb img {
        width: 50%;
        height: auto;
    }
    
    .container-moviesimdb h2 {
        font-size: 1rem;
        margin-left: 5px;   
    }
    
    .container-moviesimdb p {
        margin-left: 5px;
        font-size: 1rem;
    }
    
    .container-info {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        width: 50%;
    }


}