.list-movies {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    flex-direction: column;
    width: 60%;
    margin-left: 100px;
    
}

.list-movies li {
    font-size: 1.3rem;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.list-movies>div {
    list-style: none;
    /* text-align: center; */
}

/* img {
    height: 90px;
    width: auto;
} */

.container-movies{
    display: flex;
    /* flex-wrap: wrap; */
    width: 80%;
    padding-top: 50px;
}

form {
    display: flex;
    justify-content: center;
    padding-top: 30px;
}

.radio, button {
    margin-left: 10px;
}

form button {
    width: 5rem;
}

form>input{
    width: 300px;
    font-size: 1.3rem;
}

.prev-next-btns {
    display: flex;
    justify-content: center;
    padding-top: 20px;
    width: 100%;
}

.prev-next-btns button {
    width: 30%;
    height: 50px;
    border-radius: 50px;

}

