*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    
    font-family: 'Montserrat', sans-serif;
    background-color: rgba(36, 87, 82, 0.3);
    
}

header {
    height: 10vh;
    background-color: rgba(78, 9, 78, 0.6);
    color: white;
    
    
}

.header-ul {
    display: flex;
    height: 100%;
    justify-content: space-around;
    align-items: center;
    
}

header li {
    list-style: none;
    font-size: 50px;
    color: white;
      
}

.navlink:link {
    color: white;
}

.navlink:active {
    color: blue
}

.navlink:hover {
    color: lightblue
}

.navlink:visited {
    color: white;
}

@media (max-width: 699px) {

header {
    height: 10vh;
    background-color: rgba(78, 9, 78, 0.6);
    color: white;
    
    
}

.header-ul {
    display: flex;
    height: 100%;
    justify-content: space-around;
    align-items: center;
    
}

header li {
    list-style: none;
    font-size: 1.5rem;
    color: white;
      
}



}
